// clipboard.jsä¸è½å¤çuse å svgç»ä»¶ä¸çäºä»¶. 2018å¹´9æ6æ¥13:40:34
function getValidParentNode (node) {
  if (node.nodeName === 'use' && node.parentNode) return getValidParentNode(node.parentNode)
  if (node.nodeName === 'svg' && node.parentNode) return getValidParentNode(node.parentNode)
  return node
}

export default function handleClipboard (text, event) {
  let target = event.target
  target = getValidParentNode(target)
  const clipboard = new Clipboard(target, {
    text: () => text
  })
  clipboard.on('success', () => {
    clipboardSuccess()
    clipboard.off('error')
    clipboard.off('success')
    clipboard.destroy()
  })
  clipboard.on('error', () => {
    clipboardError()
    clipboard.off('error')
    clipboard.off('success')
    clipboard.destroy()
  })
  clipboard.onClick(event)
}
